/* global ReactDOM */

import BiobankSpecimen from './specimen';
import BiobankContainer from './container';
const args = QueryString.get(document.currentScript.src);

$(function() {
  const request = `${loris.BaseURL}/biobank/ajax/requestData.php?`;
  const submit = `${loris.BaseURL}/biobank/ajax/submitData.php?`;

  

  let display;
  switch (entity) {
    case 'specimen': 
      display = (
        <BiobankSpecimen
          specimenPageDataURL={`${request}action=getSpecimenData&barcode=${args.barcode}`}
          optionsURL={`${request}action=getFormOptions`}
          saveContainer={`${submit}action=saveContainer`}
          saveSpecimen={`${submit}action=saveSpecimen`}
          saveBarcodeListURL={`${submit}action=saveBarcodeList`}
        />
      );
    case 'container':
      display = (
        <BiobankContainer
          containerPageDataURL={`${request}action=getContainerData&barcode=${args.barcode}`}
          optionsURL={`{$request}action=getFormOptions`}
          saveContainer={`${submit}action=saveContainer`}
        />
      );
  }

  let biobankBarcode = (
    <div className="page-specimen-form">
      <div className="row">
        <div className="col-xs-12">
          {display}
        </div>
      </div>
    </div>
  );

  ReactDOM.render(biobankBarcode, document.getElementById("lorisworkspace"));
});



